@import '../mixins';

.header {
  max-width: 60rem;
  margin: auto;
  padding: 2rem;
  display: flex;
  justify-content: center;
}

.nav-list {
  display: flex;
  gap: 1rem;

  a {
    text-decoration: none;
    color: var(--color-primary-400);

    &:hover,
    &.active {
      color: var(--color-primary-800);
      text-decoration: underline;
    }
  }
}